div {
  box-sizing: border-box;
}

.nav {
  display: flex;
  background: black;
  width: 100%;
  color: #FFADBC;
  padding-left: 20px;
}

.btns {
  padding: 20px 0 0 20px;
}

.title-change-btn {
  width: 80px;
  height: 30px;
  font-size: 14px;
  border: none;
  background: #FFADBC;
  color: white;
  margin-right: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.title-sort-btn {
  width: 80px;
  height: 30px;
  font-size: 14px;
  border: none;
  background: #FFADBC;
  color: white;
  margin-right: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.list {
  padding: 0 20px 0 20px;
  border-bottom: 1px solid #863A6F;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.delete-board-btn {
  width: 80px;
  height: 30px;
  font-size: 14px;
  border: none;
  background: black;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.like {
  color: #D989B5;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.modal-title-change-btn {
  width: 80px;
  height: 30px;
  font-size: 14px;
  border: none;
  background: black;
  color: white;
  margin-right: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.create-board {
  padding: 20px 0 0 20px;
}

.create-board-input {
  width: 152px;
  height: 24px;
  margin-right: 20px;
}

.create-board-btn {
  width: 80px;
  height: 30px;
  font-size: 14px;
  border: none;
  background: #FFADBC;
  color: white;
  margin-right: 20px;
  border-radius: 5px;
  cursor: pointer;
}